import { SearchBox, FormList, FooterBar, FormRow } from 'components/index.js'

export default {
  props: {
    tableTextNameOption: {
      type: Object,
      default () {
        return {}
      }
    },
    tableShowField: {
      type: Array,
      default () {
        return []
      }
    },
    comboboxdata: {
      type: Object,
      default () {
        return {}
      }
    },
    tableType: {
      type: String,
      default: ''
    },
    tableData: {
      type: Object,
      default () {
        return {}
      }
    },
    selectId: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      KeyWord: '',
      showCheckBox: false,
      tagShow: false,
      offset: 100,
      defaultLists: [],
      tableLists: [],
      currentItem: {},
      option: {
        title: {
          text: '',
          show: true
        },
        left: {
          text: '',
          show: true
        },
        center: {
          text: '',
          show: true
        },
        right: {
          text: '',
          show: true
        }
      }
    }
  },
  mounted () {
    this.tableInit()
  },
  methods: {
    // 初始化
    tableInit () {
      this.option = Object.assign(this.option, this.tableTextNameOption)
    },
    formRowChange (item) {
      this.currentItem[item.field] = item.value
    },
    // 查看一条数据
    selectItemView (item) {
      if (this.showCheckBox) {
        return false
      } else {
        this.currentItem = item
        this.offset = 0
      }
    },
    // 获取孙表的数据
    getGrandSon () {
      let arr = []
      let allData = [...this.defaultLists]

      allData.forEach((item) => {
        if (item.PurchaseRequisitions_Id === this.selectId) {
          arr.push(item)
        }
      })

      return arr
    },
    // 关闭面板
    closeEditBlock () {
      this.offset = 100
    }
  },
  watch: {
    tableData: {
      handler (newData, oldData) {
        if (!newData.values) {
          newData.values = []
        }

        if (newData.children) {
          this.KeyWord = newData.children[0].KeyWord
          this.defaultLists = [...newData.children[0].values]
        }
      },
      immediate: true
    },
    selectId: {
      handler (newId, oldId) {
        if (newId) {
          this.tableLists = this.getGrandSon()
          this.option = Object.assign(this.option, this.tableTextNameOption)
        }
      },
      immediate: true
    }
  },
  components: {
    SearchBox,
    FormList,
    FooterBar,
    FormRow
  }
}



